<script>
import { useAccountStore, useGlobalStore } from '@/store'
import { mapState } from 'pinia'

import IconClose from '../assets/icon-close.vue'
import { REVOKE } from '../store/actions'

export default {
  name: 'ViewCancelCertModal',
  components: {
    IconClose,
  },
  props: {
    userData: {
      required: true,
    },
  },
  emits: ['back', 'close'],
  computed: {
    ...mapState(useAccountStore, ['caName', 'photoURL', 'loading']),
  },
  created() {
    this.$debug(this.userData)
  },

  methods: {
    cancel() {
      useAccountStore()[REVOKE]({ user: this.userData })
        .then(() => this.close())
        .catch((err) => {
          console.error(err)
          useGlobalStore().setErrorMessage(err.message || err)
        })
    },
    back() {
      this.$emit('back')
    },
    close() {
      this.$emit('close')
    },
  },
}
</script>

<template>
  <transition name="modal-fade">
    <div v-show="true" class="modal-backdrop">
      <div
        class="modal-wallid card_modal"
        role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
        @click.stop
      >
        <header id="modalTitle" class="modal-header">
          <slot name="header">
            {{ $t('cancelModal.title') }}

            <v-spacer />
            <button
              type="button"
              class="btn-close"
              aria-label="Close modal"
              @click="close"
            >
              <IconClose />
            </button>
          </slot>
        </header>

        <div class="modal-body text-center px-16 pb-9">
          <slot name="body">
            <v-col cols="12" md="12" class="pb-16 pt-7 px-16 text-center">
              <p v-html="$t('cancelModal.text')" />
            </v-col>
          </slot>
        </div>

        <div class="modal-footer">
          <slot name="footer">
            <v-row class="justify-end" no-gutters>
              <v-btn class="back-type-2 mr-4" @click="back()">
                {{
                  $t('button.back')
                }}
              </v-btn>
              <v-btn
                class="delete"
                :loading="loading"
                :disabled="loading"
                @click="cancel()"
              >
                {{ $t('button.cancel') }}
              </v-btn>
            </v-row>
          </slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<style lang="scss"></style>
