  box-shadow: 0 2px 4px 0 var(--very-light-grey) !important;

  & table > tbody > tr:hover {
    background-color: var(--white) !important;
  }
  & table thead > tr > th {
    padding: 0 16px;
  }
  & table tbody > tr > td,
  & table thead > tr > th,
  & table tfoot > tr > td {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: var(--charcoal-grey);
  }
  div.v-progress-linear__indeterminate.v-progress-linear__indeterminate--active
    > div.v-progress-linear__indeterminate.long.primary,
  div.v-progress-linear__indeterminate.v-progress-linear__indeterminate--active
    > div.v-progress-linear__indeterminate.short.primary {
    background-color: var(--teal-blue) !important;
  }
  & tr > td {
    svg {
      margin-bottom: -5px;
      margin-right: 4px;
    }
  }
  & table thead > tr > th:last-child,
  & table tbody > tr:not(.v-data-table__empty-wrapper) > td:last-child {
    text-align: end !important;
    padding-left: 0;
  }
  & table tbody > tr.v-data-table__empty-wrapper > td:first-child {
    padding: 0px;
  }
  & table thead > tr > th:last-child {
    padding-right: 4px;
  }
  & table tbody > tr > td:last-child {
    padding: 0 4px;
  }
  & table thead tr > th {
    font-weight: 600;
  }

  th {
    padding: 16px;
  }
}
</style>
