<script>
import { useAccountStore } from '@/store'
import { mapState } from 'pinia'

import IconClose from '../assets/icon-close.vue'

import Spinner from '@/components/Spinner.vue'
import { GET_USER } from '@/store/actions'

export default {
  name: 'ViewCredentialCardModal',
  components: {
    IconClose,
    Spinner,
  },
  props: {
    userData: {
      required: true,
    },
    templateItens: {
      required: true,
    },
    frontendProps: {
      default: () => {},
    },
  },
  emits: ['close', 'cancel'],
  data() {
    return {
      isLoaded: false,
      backTemplate: null,
      templateValues: [],
      templateName: '',
      imageArray: [],
    }
  },
  computed: {
    ...mapState(useAccountStore, ['caName', 'photoURL', 'excelTemplate', 'tid', 'caName', 'credName', 'urlPhoto', 'cardHeaders', 'customTemplateName']),
  },
  watch: {},
  async mounted() {
    console.log('mounted', GET_USER)

    const { data } = await this.$store.dispatch(GET_USER, { id: this.userData.id })
    console.log('user', data)
    this.templateName = data.template_name
    this.imageArray = data.imgArray
    const userData = JSON.parse(JSON.stringify(data.user_data))

    const frontTemplateObj = JSON.parse(JSON.stringify(data))
    delete frontTemplateObj.tables
    console.log(this.templateItens)

    console.log('frontendProps', this.frontendProps)

    if (this.templateItens) {
      for (let index = 0; index < this.templateItens.length; index++) {
        const el = this.templateItens[index]
        console.log('el', el)

        // dont add the elements email and phone
        // if (['email', 'phone'].includes(el.attr.toLowerCase())) {
        //   continue
        // }

        if (['image'].includes(el.attr.toLowerCase()) || el.inputType === 'image') {
          this.templateValues.unshift({
            attr: el.attr,
            value: userData[el.attr],
            type: el.type,
          })
        } else
          if (userData[el.attr]) {
            this.templateValues.push({
              attr: el.attr,
              value: userData[el.attr],
              type: el.type,
            })
          }
      }
      // insert an blank space after the image
      // this.templateValues.splice(1, 0, { attr: '', value: '', type: 'blank' })
    } else {
      for (const a in frontTemplateObj) {
        const val = frontTemplateObj[a]
        this.templateValues.push({ attr: a, value: val })
      }
    }
  },

  created() {

  },

  methods: {
    close() {
      this.$emit('close')
    },
    cancel() {
      this.$emit('cancel')
    },
    onImgLoad() {
      this.isLoaded = true
    },
  },

}
</script>

<template>
  <transition name="modal-fade">
    <div v-show="true" class="modal-backdrop">
      <div
        class="modal-wallid card_modal"
        role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
        @click.stop
      >
        <header id="modalTitle" class="modal-header">
          <slot name="header">
            {{ $t('viewCredential.modal.title') }}

            <v-spacer />
            <button
              type="button"
              class="btn-close"
              aria-label="Close modal"
              @click="close"
            >
              <IconClose />
            </button>
          </slot>
        </header>

        <div class="modal-body px-16 pb-16 pt-2 mb-1">
          <slot name="body">
            <v-col
              cols="12"
              md="12"
              class="pb-16 view-credential"
            >
              <v-row>
                <v-col cols="12">
                  <h2 class="title-header">
                    {{ templateName }}
                  </h2>
                </v-col>
                <v-col v-for="(item, index) in templateValues" :key="`attribute-${index}`" cols="6">
                  <div
                    v-if="item.type === 'blank'"
                  />

                  <v-img
                    v-else-if="item.type === 'image'"
                    :src="item.value"
                    width="80"
                    height="80"
                    class="mx-auto"
                    @load="onImgLoad"
                  />
                  <v-text-field
                    v-else
                    v-model="item.value"
                    :label="$te(`drivingLicense.attributes.${item.attr.toLowerCase()}`) ? $t(`drivingLicense.attributes.${item.attr.toLowerCase()}`) : item.attr"
                    readonly
                    outlined
                    dense
                    hide-details="false"
                  />
                </v-col>
                <v-col cols="12">
                  <h2 class="title-header mb-4">
                    {{ $t('viewCredential.preview') }}
                  </h2>
                  <v-img
                    v-for="(img, index) in imageArray"
                    :key="`image-${index}`"
                    :src="img"
                    class="mx-auto"
                  />
                </v-col>
              </v-row>
              <Spinner v-show="!isLoaded" />
            </v-col>
          </slot>
        </div>

        <!-- <div class="modal-footer">
          <slot name="footer">
            <v-row class="justify-end" no-gutters>
              <v-btn class="delete" @click="cancel()">
                {{ $t('button.cancel') }}
              </v-btn>
            </v-row>
          </slot>
        </div> -->
      </div>
    </div>
  </transition>
</template>

